@import url(https://fonts.googleapis.com/css?family=Rubik);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

::-webkit-scrollbar {
  width: 0px;
  background: transparent; }

body {
  background-image: url(/static/media/background.33545ec6.png);
  line-height: 2 !important;
  font-family: 'Rubik', sans-serif !important;
  height: 0px !important; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.notify {
  position: absolute !important;
  display: block !important;
  left: 50%;
  top: 40px;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  z-index: 10 !important; }

.login-container {
  margin-top: 25vh; }

.register-container {
  margin-top: 20vh; }

.authentication-link {
  margin-top: 3vh !important; }

a, a:visited, a:hover, a:active {
  color: inherit; }

.avatar-container {
  border-radius: 100%;
  border: 1px solid #9e9e9e;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #dcf8c6;
  color: black; }

.avatar-small {
  height: 35px;
  width: 35px; }

.avatar-medium {
  height: 45px;
  width: 45px; }

.message-loader {
  top: 2.28571429rem !important; }

.home-container {
  margin-top: 10px;
  box-shadow: -1px 2px 21px -4px rgba(0, 0, 0, 0.65);
  min-width: 615px;
  background-color: white; }

.scrollable {
  overflow: auto; }

.row-container {
  height: 90vh; }

.profile-tab {
  display: flex;
  align-items: center;
  background-color: #f7f7f7;
  height: 50px;
  border-bottom: 1px solid #e8e8e8; }

.user-search {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e8e8e8;
  font-size: 14px; }

.user-container {
  border-right: 1px solid #e8e8e8;
  max-height: 90vh !important; }

.message-container {
  /** Display flex, and set the direciton as column */
  display: flex;
  flex-direction: column;
  background-image: url(data:image/png;base64,);
  overflow: hidden;
  max-height: 90vh !important; }

.send-message {
  background-color: #f7f7f7;
  height: 60px;
  border-top: 1px solid #e8e8e8; }

.message-input {
  margin-top: 10px;
  width: 100%; }

.messages {
  /*
        Flex auto, will automatically allocate all the free space that is available in the container
        to this element. This will cause the send-message div to be automatically placed on the bottom,
        as all items in a flex container has a default flex property of 1.
    */
  flex: auto;
  max-height: none !important;
  height: 500px; }

@-webkit-keyframes pop-in {
  0% {
    -webkit-transform: scale(0.85); }
  100% {
    -webkit-transform: scale(1); } }

@keyframes pop-in {
  0% {
    -webkit-transform: scale(0.85); }
  100% {
    -webkit-transform: scale(1); } }

.user-card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e8e8e8;
  cursor: pointer; }

.user-card {
  min-width: 100px;
  display: flex;
  flex-direction: row; }

.user-card-text-group {
  margin-top: 15px;
  margin-left: 20px; }

.user-card-text-group h1 {
  font-weight: 400;
  font-size: 13px; }

.user-card-text-group p {
  font-size: 11px;
  color: #a6a6a6; }

.close-user-card {
  height: 15px;
  width: 15px; }

.close-user-card:hover {
  -webkit-transform: scale(1.25);
          transform: scale(1.25); }

.count-bubble {
  margin-right: 10px;
  height: 20px;
  width: 20px;
  font-size: 10px;
  background-color: #8feba9;
  border-radius: 12px;
  text-align: center;
  animation: pop-in 0.2s;
  -webkit-animation: pop-in 0.2s;
  -moz-animation: pop-in 0.2s;
  -ms-animation: pop-in 0.2s; }

.user-card-options {
  display: flex;
  flex-direction: row;
  margin-left: auto;
  margin-top: 15px; }

@-webkit-keyframes pop-in {
  0% {
    -webkit-transform: scale(0.85); }
  100% {
    -webkit-transform: scale(1); } }

@keyframes pop-in {
  0% {
    -webkit-transform: scale(0.85); }
  100% {
    -webkit-transform: scale(1); } }

.message-card-container-right {
  max-width: 400px;
  margin-left: auto;
  margin-right: 100px; }

.message-card-container-left {
  max-width: 400px;
  margin-left: 75px; }

.message-card-left {
  background-color: white; }

.message-card-right {
  background-color: #dcf8c6; }

.message-card {
  word-break: break-all;
  min-width: 70px;
  box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
  border: 1px solid #e6e6e6;
  margin: 10px;
  margin-top: 20px !important;
  padding: 10px;
  border-radius: 5%;
  animation: pop-in 0.2s;
  -webkit-animation: pop-in 0.2s;
  -moz-animation: pop-in 0.2s;
  -ms-animation: pop-in 0.2s; }

.message-card h6 {
  font-size: 14px; }

.message-card p {
  font-size: 13px; }

.message-card small {
  margin-top: 8px;
  float: right;
  font-size: 8px;
  color: rgba(0, 0, 0, 0.45); }

.message-user {
  margin-bottom: 10px; }

